body {
    margin: 0;
    padding: 0;
    /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;*/
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body, p, h1, h2, h3, h4 {
    margin: 0;
    padding: 0;
    color: #333333;
    font-weight: 500;
}

h1 {
    line-height: 50px;
    margin: 0 0 10px;
    min-height: 80px;
}

h2 {
    margin-bottom: 15px;
    margin-top: 35px;
    font-size: 26px;
    line-height: 25px;
    min-height: 30px;
}

h3 {
    margin-bottom: 10px;
    margin-top: 20px;
    line-height: 25px;
    min-height: 30px;
}

p {
    line-height: 25px;
    padding: 15px 0;
}

a {
    color: #333333;
    text-decoration: none;
}

p a, li a {
    color: #3072C4;
}

p a:hover, li a:hover {
    color: #3072C4;
    text-decoration: underline;
}

ol, ul {
    margin-top: 0;
}

li {
    margin: 10px auto;
    line-height: 25px;
}

button, input {
    outline: none;
    border-color: transparent;
    background-color: transparent;
}

hr {
    border-style: solid;
    border-width: 0.5px;
    border-color: #ebebeb;
    margin: 50px 0;
}

